"use client";
import Image from "next/image";
import { Check, Share2 } from "lucide-react";
import Link from "next/link";
import { useQuery } from "@tanstack/react-query";

import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import PriceFormatter from "@/components/format-price/PriceFormatter";
import useStore from "@/context/store";
import { ArrowRightIcon, CopyIcon } from "@/assets/icons";
import { copyToClipboard, formatUzbekNumber } from "@/utils";
import OrderListIcon from "@/assets/icons/OrderListIcon";
import { useRequireAuth } from "@/hooks";
import formStore from "@/context/form-store";
import { Section } from "@/components/section";
import ArrowLeftLongIcon from "@/assets/icons/ArrowLeftLongIcon";
import { getAgentAdress } from "@/api";
import { DOMAIN } from "@/constants";

const CartFinalPage = () => {
  const selected = useStore((state) => state.selected);
  const { cartForm, deliveryForm, contactForm } = formStore();
  const auth = useRequireAuth();
  const { data: contrAgent } = useQuery({
    queryKey: ["contragents", contactForm?.contrAgentId],
    queryFn: () => getAgentAdress(contactForm?.contrAgentId || ""),
    enabled: !!contactForm?.contrAgentId,
  });
  if (!auth) return null;
  const handleFinal = () => {
    const payload = { ...cartForm, ...deliveryForm, ...contactForm };
    console.log(payload);
  };
  console.log(cartForm);

  return (
    <div className="space-y-[23px]">
      {/* contact */}
      <Section className="rounded-none shadow-sectionShadow">
        <Link
          href={"/cart/delivery"}
          className="flex items-center gap-[7px] text-stoneCold text-xs group"
        >
          <span className="group-hover:underline">
            Назад к способу доставки
          </span>
          <ArrowLeftLongIcon
            width={15}
            height={11}
            className="text-textColor"
          />
        </Link>
        <div className="flex items-center gap-3 my-[23px]">
          <h3 className="font-normal text-stoneCold text-[17px] leading-[20.5px]">
            1. Контактная информация
          </h3>
          <div className="bg-greenLight w-[18px] h-[18px] rounded-full flex items-center justify-center">
            <Check className="text-white" strokeWidth={5} size={8} />
          </div>
        </div>
        <div className="border border-superSilver grid grid-cols-2 bg_final p-6">
          <div className=" flex flex-col gap-4">
            <div className="flex flex-col gap-1.5">
              <span className="text-weekColor font-normal text-xs">
                Контрагент
              </span>
              <p className="text-stoneCold text-sm font-normal">
                {contrAgent?.name} <span>ИНН {contrAgent?.inn}</span>
              </p>
            </div>
            <div className="flex flex-col gap-1.5">
              <span className="text-weekColor font-normal text-xs">
                Телефон
              </span>
              <p className="text-stoneCold text-sm font-normal">
                {formatUzbekNumber(contactForm?.phone.toString())}
              </p>
            </div>
          </div>
          <div className=" flex flex-col gap-4">
            <div className="flex flex-col gap-1.5">
              <span className="text-weekColor font-normal text-xs">
                Получатель
              </span>
              <p className="text-stoneCold text-sm font-normal">
                {contactForm?.lastname} {contactForm?.firstname}
              </p>
            </div>
            <div className="flex flex-col gap-1.5">
              <span className="text-weekColor font-normal text-xs">E-mail</span>
              <p className="text-stoneCold text-sm font-normal">
                {contactForm?.email}
              </p>
            </div>
          </div>
        </div>
      </Section>
      {/* delivery products */}
      <Section className="rounded-none shadow-sectionShadow">
        <div className="flex items-center gap-3 mb-[23px]">
          <h3 className="font-normal text-stoneCold text-[17px] leading-[20.5px]">
            2. Доставка
          </h3>
          <div className="bg-greenLight w-[18px] h-[18px] rounded-full flex items-center justify-center">
            <Check className="text-white" strokeWidth={5} size={8} />
          </div>
        </div>
        <div>
          <h3 className="text-textColor font-semibold text-lg leading-[27px] mb-3">
            Склад{" "}
            {deliveryForm?.deliveryMethod === "Самовывоз"
              ? "Самовывоз"
              : "отгрузки"}
            : Ташкент{" "}
            <span className="text-darkSoul">({selected?.length})</span>
          </h3>
          <p className="text-sm text-darkSoul">
            Доставка{" "}
            <span
              className={
                deliveryForm?.deliveryMethod === "До пункта выдачи"
                  ? "uppercase"
                  : "lowercase"
              }
            >
              {deliveryForm?.deliveryMethod === "До пункта выдачи"
                ? "ПВЗ"
                : deliveryForm?.deliveryMethod}
              :
            </span>
            <span className="ml-1 underline underline-offset-2 text-cerulean">
              {deliveryForm?.deliveryMethod === "До пункта выдачи" ? (
                "Ташкент"
              ) : (
                <span>
                  {deliveryForm?.address?.fullAddress
                    .split(",")
                    .map((part) => part.trim())
                    .reverse()
                    .join(", ")}
                  , {deliveryForm?.address?.street} ,{" "}
                  {deliveryForm?.address?.house}
                </span>
              )}
            </span>
          </p>
          <Table className="border border-gray-300 rounded-none overflow-hidden w-full mt-6 ">
            <TableHeader>
              <TableRow className="bg-gray-100 text-left">
                <TableHead className="p-4 text-stoneCold font-semibold text-sm">
                  Наименование товара
                </TableHead>
                <TableHead className="p-4 text-stoneCold font-semibold text-sm">
                  Артикул
                </TableHead>
                <TableHead className="p-4 text-stoneCold font-semibold text-sm">
                  Кол-во
                </TableHead>
                <TableHead className="p-4 text-end text-stoneCold font-semibold text-sm">
                  Цена
                </TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {selected.map((product) => (
                <TableRow key={product.id}>
                  <TableCell className="border-r p-[14px]">
                    <div className="flex items-center gap-2 justify-start">
                      <div className="w-auto h-auto">
                        <Image
                          src={`${DOMAIN}/${product.mainImage}`}
                          alt={product.title}
                          width={65}
                          height={65}
                          className=" aspect-square"
                        />
                      </div>
                      <p className="text-stoneCold text-sm font-normal text-wrap flex-1">
                        {product.title}
                      </p>
                    </div>
                  </TableCell>
                  <TableCell className="p-[14px] border-r text-xs text-textColor">
                    <div className="flex items-center gap-2 justify-between">
                      <p className="flex-1">{product.articul}</p>
                      <span
                        className="cursor-pointer text-explosiveGrey hover:text-cerulean hoverEffect"
                        onClick={() =>
                          copyToClipboard(
                            product.articul,
                            `Артикул ${product.articul} скопирован в буфер обмена`
                          )
                        }
                      >
                        <CopyIcon />
                      </span>
                    </div>
                  </TableCell>
                  <TableCell className="p-[14px] text-xs font-normal text-textColor border-r">
                    <p className="text-xs text-textColor font-normal text-center">
                      {product.quantity}
                    </p>
                  </TableCell>
                  <TableCell className="p-[14px] text-xs font-normal text-textColor border-r text-end">
                    <PriceFormatter
                      amount={product.price}
                      className="text-xs text-textColor font-normal"
                    />
                  </TableCell>
                </TableRow>
              ))}
              {deliveryForm?.deliveryMethod !== "Самовывоз" && (
                <TableRow className="w-full">
                  <TableCell className="p-[14px] text-sm font-semibold text-textColor ">
                    В т.ч. доставка
                  </TableCell>
                  <TableCell
                    className="p-[14px]  font-semibold text-sm text-textColor text-right"
                    colSpan={3}
                  >
                    будет рассчитана менеджером
                  </TableCell>
                </TableRow>
              )}
              <TableRow className="w-full">
                <TableCell className="p-[14px] text-sm font-semibold text-textColor ">
                  Сумма
                </TableCell>
                <TableCell
                  className="p-[14px]  text-xs font-semibold text-textColor text-right"
                  colSpan={3}
                >
                 <PriceFormatter amount={cartForm?.total} className="text-sm"/>
                </TableCell>
              </TableRow>
            </TableBody>
          </Table>
        </div>
      </Section>
      <div className=" bg-white border shadow-sectionShadow py-[23px] px-[20px] mb-6">
        {selected.map((product, index) => (
          <div key={product.id}>
            <div className="border-b border-superSilver text-center mb-5">
              <h3 className="relative text-textColor font-normal inline-block text-lg pb-4">
                {index + 1}. Заказ № UZ001604901 (НДС не облагается)
                <span className="absolute bottom-0 left-0 w-full h-[4px] bg-gradient-to-r from-cerulean to-blue-500"></span>
              </h3>
            </div>

            <div className="border border-gray-300 rounded-none border-t-0">
              <div className="flex items-center justify-between gap-2 px-3 py-4">
                <p className="font-semibold text-sm text-textColor">Сумма</p>
                <p className="font-semibold text-sm text-textColor">
                  16 576 416 сум
                </p>
              </div>
            </div>
            <div className="flex items-center gap-2 my-4">
              <button
                onClick={() => console.log("Clicked")}
                className="bg-white  hover:opacity-90 transition-opacity py-2 px-6 text-base font-semibold text-cerulean border border-cerulean flex items-center justify-center gap-2"
              >
                <ArrowRightIcon />
                Перейти к заказу
              </button>
              <button
                onClick={() => console.log("Clicked")}
                className="bg-white  hover:opacity-90 transition-opacity p-2 text-base font-semibold text-cerulean border border-cerulean flex items-center justify-center"
              >
                <Share2 className="w-6 h-6" />
              </button>
            </div>
          </div>
        ))}
      </div>
      <div className=" bg-white border shadow-sectionShadow py-[23px] px-[20px] text-center">
        <h3 className="font-normal text-textColor text-[29px] leading-[35px] mb-7">
          Остались вопросы?
        </h3>
        <p className="text-textColor text-sm font-normal mb-6">
          По телефону{" "}
          <Link className="text-cerulean font-semibold" href={"#"}>
            +998 55 508 0660
          </Link>{" "}
          или{" "}
          <Link className="text-cerulean font-semibold" href={"#"}>
            @NagSalesBot
          </Link>
        </p>
        <p className="text-textColor text-sm font-normal">
          Менеджер свяжется с вами, чтобы уточнить стоимость, состав и детали
          заказа, согласовать и подтвердить его.
        </p>
        <p className="text-textColor text-sm font-normal mb-6">
          Статус Вашего заказа вы можете отслеживать в личном кабинете.
        </p>
        <p className="flex items-center justify-center gap-2 text-textColor text-sm font-normal">
          <OrderListIcon className="w-6 h-6" />
          Cчёт после формирования заказа будет выгружен в личный кабинет!
        </p>
      </div>
    </div>
  );
};

export default CartFinalPage;
